            ></v-select>
          </v-flex>
          <v-flex xs6 sm4 md2>
            <v-text-field v-model="base_below_sn" label="åº•åº§SNå·ç "></v-text-field>
          </v-flex>
          <!-- <v-flex xs6 sm4 md2>
            <v-text-field v-model="base_above_sn" label="ä¸Šç›–SNå·ç "></v-text-field>
