          <v-flex xs6 sm4 md2>
            <v-text-field v-model="blue_tooth" label="è“ç‰™åç§°"></v-text-field>
          </v-flex>
          <v-flex xs6 sm4 md2>
            <v-select
              @change="info"
              menu-props="auto"
              v-model="status"
              :items="['å…¨éƒ¨è®¾å¤‡', 'å·²æ¿€æ´»', 'æœªä½¿ç”¨']"
              label="è®¾å¤‡çŠ¶æ€"
              required
            ></v-select>
          </v-flex>
          <v-btn :loading="loader" :disabled="loader" color="primary" @click="info">ç­›é€‰è®¾å¤‡</v-btn>
        </v-layout>
      </v-card-title>
      <!-- 
        headers : è¡¨å¤´
        itemsï¼š è¡¨æ ¼å†…å®¹
        total-items ï¼š æ€»å…±çš„æ•°æ®æ•°é‡ ï¼ˆæ‰‹åŠ¨è®¾ç½®é¡¹ç›®çš„æ€»æ•°ï¼Œå°±å¯ä»¥ç¦ç”¨å†…ç½®æ’åºå’Œåˆ†é¡µï¼‰
        disable-initial-sort åœ¨åˆå§‹æ¸²æŸ“æ—¶ç¦ç”¨é»˜è®¤æ’åº
        :hide-actions="true" éšè—è¡¨æ ¼æ“ä½œ
        :dark="true" æš—ä¸»é¢˜
        :loading="true" åŠ è½½è¿›åº¦æ¡
      -->
      <v-data-table
        v-model="selected"
        :headers="headers"
        :items="desserts"
        class="elevation-1"
        :total-items="allList"
        :rows-per-page-text="`æ¯é¡µè¡Œæ•°`"
        :rows-per-page-items="pagingList"
        :disable-initial-sort="true"
        :loading="loader"
        @update:pagination="info"
      >
        <template slot="headers" slot-scope="props">
          <tr>
            <th>
              <v-checkbox
                color="error"
                :input-value="props.all"
                :indeterminate="props.indeterminate"
                primary
                hide-details
                @click.native="toggleAll"
              ></v-checkbox>
            </th>
            <th
              v-for="header in props.headers"
              :key="header.text"
              :class="['column sortable', pagination.descending ? 'desc' : 'asc', header.value === pagination.sortBy ? 'active' : '']"
              @click="changeSort(header.value)"
            >
              <!-- <v-icon small>arrow_upward</v-icon> -->
              {{ header.text }}
            </th>
          </tr>
        </template>
        <template v-slot:items="props">
          <tr>
            <td :active="props.selected" @click="props.selected = !props.selected">
              <v-checkbox color="error" :input-value="props.selected" primary hide-details></v-checkbox>
            </td>
            <!-- <td class="text-md-center text-xs-centre" >{{ props.index+1 }}</td> -->
            <td class="text-xs-centre text-md-center">{{ props.item.base_below_sn }}</td>
            <td class="text-xs-centre text-md-center">{{ props.item.blue_tooth }}</td>
            <td class="text-xs-centre text-md-center">
              <span v-if="props.item.agent_name !== ''">{{ props.item.agent_name }}</span>
              <i v-else>- - -</i>
            </td>
            <!-- <td class="text-xs-centre">{{ props.item.base_above_sn }}</td> -->

            <td class="text-xs-centre text-md-center">{{ props.item.create_time | time }}</td>
            <td class="text-xs-centre text-md-center">{{ props.item.update_time | time }}</td>
            <td class="text-xs-centre text-md-center">
              <span class="green--text darken-4" v-if="props.item.status==1">ä½¿ç”¨ä¸­</span>
              <span v-if="props.item.status !== 1">æœªæ¿€æ´»</span>
              <!-- {{ props.item.status==1?"ä½¿ç”¨ä¸­":"æœªæ¿€æ´»" }} -->
            </td>
            <td class="text-xs-centre text-md-center">
              <span
                class="green--text darken-4"
                v-if="props.item.user_name !== ''"
              >{{ props.item.user_name }}</span>
              <i v-else>- - -</i>
            </td>
            <td class="text-xs-centre text-md-center">
              <v-menu
                bottom
                dark
                origin="center center"
                :close-delay="100"
                transition="slide-x-transition"
              >
                <template v-slot:activator="{ on }">
                  <v-btn color="primary" dark v-on="on">æ“ä½œé€‰é¡¹</v-btn>
                </template>
                <v-list>
                  <template v-for="(item,index) in root_path">
                    <v-list-tile
                      :key="index+100"
                      v-if="item.url == '/api/v1/manages/ed_record' && props.item.user_name !== ''"
                      
                      @click="ED_data( props.item.user_id )"
                    >
                      <v-list-tile-title>EDè®°å½•</v-list-tile-title>
                    </v-list-tile>
                  </template>

                  <template v-for="(item,index) in root_path">
                    <v-list-tile
                      :key="index+100"
                      v-if="item.url == '/api/v1/manages/ehs' && props.item.user_name !== ''"
                      
                      @click="EHS_data( props.item.user_id )"
                    >
                      <v-list-tile-title>EHSè®°å½•</v-list-tile-title>
                    </v-list-tile>
                  </template>

                  <template v-for="(item,index) in root_path">
                    <v-list-tile
                      :key="index+100"
                      v-if="item.url == '/api/v1/manages/iief5' && props.item.user_name !== ''"
                      
                      @click="iief5_data( props.item.user_id )"
                    >
                      <v-list-tile-title>IIEF5è®°å½•</v-list-tile-title>
                    </v-list-tile>
                  </template>

                  <template v-for="(item,index) in root_path">
                    <v-list-tile
                      :key="index+100"
                      v-if="item.url == '/api/v1/manages/cases_file' && props.item.user_name !== ''"
                      
                      @click="files_data( props.item.user_id )"
                    >
                      <v-list-tile-title>ç—…ä¾‹æ¡£æ¡ˆ</v-list-tile-title>
                    </v-list-tile>
                  </template>

                  <template v-if="props.item.user_name !== ''">
                    <v-list-tile  @click="device_top( props.item.user_id )">
                      <v-list-tile-title>å…³è”ä¸Šç›–</v-list-tile-title>
                    </v-list-tile>
                  </template>

                  <template v-for="(item,index) in root_path">
                    <v-list-tile
                      :key="index+100"
                      v-if="item.url == '/api/v1/device/delete_sn'"
                      
                      @click="device_( props.item.id )"
                    >
                      <v-list-tile-title class="red--text">åˆ é™¤è®¾å¤‡</v-list-tile-title>
                    </v-list-tile>
                  </template>
                </v-list>
              </v-menu>

              <!-- <v-btn v-if="props.item.user_name !== ''" @click="ED_data( props.item.user_id )">EDè®°å½•</v-btn>
            <v-btn v-if="props.item.user_name !== ''" @click="EHS_data( props.item.user_id )">EHSè®°å½•</v-btn>
            <v-btn
              v-if="props.item.user_name !== ''"
              @click="iief5_data( props.item.user_id )"
            >iief5è®°å½•</v-btn>
              <v-btn v-if="props.item.user_name !== ''" @click="files_data( props.item.user_id )">ç—…ä¾‹æ¡£æ¡ˆ</v-btn>-->
              <!-- <template v-for="(item,index) in root_path">
            <v-btn :key="index+150" v-if="item.url == '/api/v1/device/delete_sn' || props.item.user_name == ''" color="error" @click="device_delete( props.item.user_id )">åˆ é™¤è®¾å¤‡</v-btn>
              </template>-->
              <!-- <i v-if="props.item.user_name == ''">è®¾å¤‡æœªå…³è”ç”¨æˆ·</i> -->
            </td>
          </tr>
        </template>
        <!-- å½“æ²¡æœ‰æ•°æ®çš„æ—¶å€™ï¼Œå¯ä»¥ä½¿ç”¨ no-data æ’æ§½æ˜¾ç¤ºä¸€æ®µè‡ªå®šä¹‰HTMLã€‚ -->
        <template v-slot:no-data>
          <!-- <v-alert :value="true" color="warning" icon="priority_high" outline>æ²¡æœ‰æ›´å¤šæ•°æ®äº† :(</v-alert> -->
          <p class="p_text">æ²¡æœ‰æ›´å¤šæ•°æ®äº† :(</p>
        </template>
        <!-- å¦‚æœä½ è¿˜æƒ³æ·»åŠ ä¸€äº›é¢å¤–çš„åŠŸèƒ½ï¼Œæ¯”å¦‚æ¯åˆ—è¿‡æ»¤æˆ–è€…æœç´¢ï¼Œä½ å¯ä»¥ä½¿ç”¨ footer æ’æ§½ã€‚ -->
        <!-- <template v-slot:footer>
          <td :colspan="headers.length">
            <strong>æ€»è®¡ï¼š</strong>
          </td>
        </template>-->
        <!-- ä½ å¯ä»¥ä½¿ç”¨page-text æ’æ§½æ¥è‡ªå®šä¹‰æ˜¾ç¤ºèŒƒå›´ä»¥åŠæ€»æ¡ç›®ã€‚ -->
        <template
          v-slot:pageText="props"
        >å½“å‰ {{ props.pageStart }} - {{ props.pageStop }} æ€»å…± {{ props.itemsLength }}</template>
      </v-data-table>
      <v-btn color="error" flat v-if="selected.length > 0" @click="device_list()">ğŸ˜« æ‰¹é‡åˆ é™¤</v-btn>
    </v-card>
    <!-- åˆ é™¤ç¡®è®¤æ¡† -->
    <v-layout row justify-center>
      <v-dialog v-model="dialog" persistent max-width="290">
        <v-card>
          <v-card-title class="headline">åˆ é™¤æé†’</v-card-title>
          <v-card-text>ç¡®è®¤è¦åˆ é™¤é€‰ä¸­çš„è®¾å¤‡å—ï¼Ÿç¡®è®¤åˆ é™¤å°†ä¸å¯æ¢å¤ã€‚è¯·è°¨æ…æ“ä½œï¼</v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="green darken-1" flat="flat" @click="dialog = false">å–æ¶ˆ</v-btn>
            <v-btn color="green darken-1" flat="flat" @click="device_delete()">ç¡®è®¤</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-layout>
  </div>
</template>
<script>
import Breadcrumbs from "../../../components/breadcrumbs/breadcrumbs.vue";

export default {
  components: {
    Breadcrumbs
  },
  data() {
    return {
      // ç­›é€‰çŠ¶æ€
      agent_id: null,
      base_below_sn: null,
      base_above_sn: null,
      blue_tooth: null,
      status: null,

      agent_list: [],
      agent_list_data: [],
      pagingList: [10, 20, 30, 40, 50, { text: "100", value: 100 }],
      loader: false,
      page: 0,
      allList: 0,
      items: [
        {
          text: "é¦–é¡µ",
          disabled: false,
          to: "/home"
        },
        {
          text: "åº•åº§åˆ—è¡¨",
          disabled: false,
          to: "/deviceList"
        }
      ],
      pagination: {
        sortBy: "name"
      },
      selected: [],
      headers: [
        // { text: "#", align: "left", sortable: false, value: false},
        { text: "è®¾å¤‡åº•åº§sn", value: "fat", sortable: false },
        { text: "è“ç‰™", value: "protein", sortable: false },
        { text: "æ‰€å±ä»£ç†å•†", value: "calories", sortable: false },

        // { text: "è®¾å¤‡ä¸Šç›–sn", value: "carbs", sortable: false },

        { text: "åˆ›å»ºæ—¶é—´", value: "iron", sortable: false },
        { text: "æ›´æ–°æ—¶é—´", value: "iron", sortable: false },
        { text: "çŠ¶æ€", value: "iron", sortable: false },
        { text: "ç”¨æˆ·è´¦å·", value: "iron", sortable: false },
        { text: "æ“ä½œ", value: "iron", sortable: false }
      ],
      desserts: [],
      root_path: this.$cookie.getLocalStorage("root_path").root_path,
      level_type: this.$cookie.getLocalStorage("root_path").level,
      dialog: false,
      id: []
    };
  },
  mounted: function() {
    this.agent_data();
  },
  methods: {
    // å…³è”ä¸Šç›–
    device_top(user_id) {
      this.$router.push({
        path: `/deviceList_`,
        query: {
          user_id: user_id
        }
      });
    },
    // å…¨é€‰
    toggleAll() {
      if (this.selected.length) this.selected = [];
      else this.selected = this.desserts.slice();
    },
    // å•é€‰
    changeSort(column) {
      if (this.pagination.sortBy === column) {
        this.pagination.descending = !this.pagination.descending;
      } else {
        this.pagination.sortBy = column;
        this.pagination.descending = false;
      }
    },
    // åˆ é™¤ä¸€æ¡
    device_(id) {
      this.dialog = true;
      this.id.push(id);
    },
    // æ‰¹é‡åˆ é™¤
    device_list() {
      this.id = [];
      this.dialog = true;
      this.selected.forEach(item => {
        this.id.push(item.id);
      });
    },
    // åˆ é™¤æ“ä½œ
    device_delete() {
      this.$axios({
        method: "post",
        url: this.$apis.URL.device_delete_sn,
        data: {
          ids: this.id,
          is_base: 1
        }
      })
        .then(response => {
          if (response.data.result === 0) {
            this.dialog = false;
            this.id = [];
            this.$Dialog.getProductCheckResult_({text: "å·²åˆ é™¤è¯¥è®¾å¤‡ï¼",timeout: 3000, snackbar: true });
            this.info();
          } else {
            this.$Dialog.getProductCheckResult_({text:  "ğŸ˜¡" + response.data.message,timeout: 3000, snackbar: true });
          }
        })
        .catch(error => {
          this.$Dialog.getProductCheckResult_({text: "ğŸ˜¡" + error,timeout: 3000, snackbar: true });
        });
    },
    // ä»£ç†åˆ—è¡¨
    agent_data() {
      this.$axios({
        method: "post",
        url: this.$apis.URL.manages_query_agent,
        data: {
          id: -1,
          level: 2
        }
      })
        .then(response => {
          this.agent_list.push("å…¨éƒ¨ä»£ç†");
          this.agent_list_data = response.data.datas;
          if (response.data.result === 0) {
            this.agent_list_data.forEach(item => {
              this.agent_list.push(item.user_name);
            });
          } else {
            this.$Dialog.getProductCheckResult_({text: response.data.message,timeout: 3000, snackbar: true });
          }
        })
        .catch(error => {
          this.$Dialog.getProductCheckResult_({text: "ğŸ˜¡" + error,timeout: 3000, snackbar: true });
        });
    },
    // ED è®°å½•
    ED_data(id) {
      this.$router.push({
        path: `/ED`,
        query: {
          user_id: id
        }
      });
    },
    // EHS è®°å½•
    EHS_data(id) {
      this.$router.push({
        path: `/EHS`,
        query: {
          user_id: id
        }
      });
    },
    // iief5è®°å½•
    iief5_data(id) {
      this.$router.push({
        path: `/iief5`,
        query: {
          user_id: id
        }
      });
    },
    // ç—…å†æ¡£æ¡ˆ
    files_data(id) {
      this.$router.push({
        path: `/files`,
        query: {
          user_id: id
        }
      });
    },
    loader_() {
      this.loader = true;
      setTimeout(() => (this.loader = false), 500);
    },
    // è®¾å¤‡åˆ—è¡¨
    info(event) {
      this.loader = true;
      let agent_id_ = null;
      this.agent_list_data.forEach(item => {
        if (this.agent_id == item.user_name) {
          agent_id_ = item.id;
          return false;
        }
      });
      this.$axios({
        method: "post",
        url: this.$apis.URL.device_query_sn,
        data: {
          agent_id: agent_id_,
          base_below_sn: this.base_below_sn,
          base_above_sn: this.base_above_sn,
          blue_tooth: this.blue_tooth,
          status:
            this.status == "å…¨éƒ¨çŠ¶æ€"
              ? null
              : this.status == "å·²æ¿€æ´»"
              ? 1
              : this.status == "æœªä½¿ç”¨"
              ? -1
              : null,
          page: this.page,
          page_count: (typeof(event)=="undefined"?undefined:event.rowsPerPage),
          type: 1
        }
      })
        .then(response => {
          if (response.data.result === 0) {
            if (this.$route.query.user_id !== undefined) {
              this.uID_info(this.$route.query.user_id);
            } else {
              this.desserts = response.data.datas.list;
              this.allList = response.data.datas.total_number;
            }
          } else {
            this.$Dialog.getProductCheckResult_({text: response.data.message,timeout: 3000, snackbar: true });
          }
          this.loader = false;
        })
        .catch(error => {
          this.$Dialog.getProductCheckResult_({text: error,timeout: 3000, snackbar: true });
        });
    },
    // å¸¦å‚ç­›é€‰è®¾å¤‡åˆ—è¡¨
    uID_info(user_id) {
      this.$axios({
        method: "post",
        url: this.$apis.URL.device_query_sn,
        data: {
          user_id: user_id - 0,
          type: 1
        }
      })
        .then(response => {
          if (response.data.result === 0) {
            this.desserts = response.data.datas.list;
          } else {
            this.$Dialog.getProductCheckResult_({text: response.data.message,timeout: 3000, snackbar: true });
          }
        })
        .catch(error => {
          this.$Dialog.getProductCheckResult_({text: error,timeout: 3000, snackbar: true });
        });
    }
  },
  watch: {}
};
</script>

<style lang="less" scoped>
.flex {
